<template>
    <div class="wrap">

        <!--  -->
        <ul class="order-type-select">
            <li
                v-for="(orderType, orderTypeIdx) in orderTypeArr"
                :class="{'active': orderType.active}"
                @click="orderTypeFn(orderTypeIdx)">{{orderType.text}}</li>
        </ul>

        <!--  -->
        <div class="order-empty" v-if="orderEmpty">
            <router-link to="/home" class="btn-index"></router-link>
        </div>

        <div v-else>
              <!--  -->
            <div class="order" v-for="(order, orderIdx) in orderArr">
                <!--  -->
                <router-link class="order-header" :to="{path: '/orderDetail', query: {orderId: order.orderId}}">
                    <div class="header-ctn">{{order.orderCode}}</div>
                    <div class="header-ctn">{{orderStatusTextFn(order.orderStatus, order.evaluateFlag)}}</div>
                </router-link>
                <!--  -->
                <div class="order-firm">
                    <router-link class="firm-name" :to="{path: '/storeDetail', query: {storeId: order.supplierStoreId}}">{{order.supplierCompanyName}}</router-link>
                </div>
                <!--  -->
                <div class="order-product">
                    <router-link class="product-detail" v-for="(product, productIdx) in order.listOrderGoods" :key="productIdx" :to="{path: '/goodsDetail', query: {goodsId: product.goodsId}}">
                        <!--  -->
                        <div class="product-img">
                            <img :src="product.goodsImageUrl">
                        </div>
                        <!--  -->
                        <div class="product-infor">
                            <div class="infor-div">
                                <!--  -->
                                <div class="product-name">{{product.goodsName}}</div>
                                <!--  -->
                                <div class="product-price"><span>{{productPriceInteger(orderIdx, productIdx)}}</span>.{{productPriceDecimals(orderIdx,
                                    productIdx)}}
                                </div>
                            </div>

                            <!--  -->
                            <ul class="infor-other">
                                <!--  -->
                                <li>{{product.goodsSpec}}</li>
                                <!--  -->
                                <li>{{product.goodsProducer}}</li>
                                <!--  -->
                                <li>{{product.goodsProductLotCode}}</li>
                            </ul>

                            <!--  -->
                            <div class="product-amount">
                                <span class="amount-label">{{product.shoppingType === 1 ? '' : ''}}</span>x{{product.shoppingType === 1 ? product.goodsOpenNum : product.goodsCompleteNum}}
                            </div>
                        </div>
                    </router-link>
                </div>
                <!--  -->
                <div class="more" v-if="order.listOrderGoods.length > 5">
                    <div class="btn-more"></div>
                </div>
                <!--  -->
                <div class="order-total">{{order.listOrderGoods.length}}<span class="red"><span>{{orderPriceInteger(orderIdx)}}</span>.{{orderPriceDecimals(orderIdx)}}</span></div>
                <div class="order-opt">
                    <!-- <div class="btn btn-default"
                    v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"></div> -->
                    <div class="btn btn-default"
                        v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"
                        @click="popCancelFn(orderIdx)"></div>
                    <router-link class="btn btn-primary" 
                        to="/orderPay"
                        v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"></router-link>
                    <div class="btn btn-default"
                        v-if="(orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '') && order.orderPartialExpressNumber <= 1"
                        @click="logisticsFn(order.orderId)"></div>
                    <router-link class="btn btn-default" 
                        v-if="(orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '') && order.orderPartialExpressNumber > 1"
                        :to="{path: '/orderBatches', query: {orderCode: order.orderCode, orderId: order.orderId}}"></router-link>
                    <div class="btn btn-primary"
                        v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"
                        @click="popRemindFn(order.orderId)"></div>
                    <div class="btn btn-default"
                        v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"
                        @click="popPostponeFn(orderIdx, order.orderId)"></div>
                    <div class="btn btn-primary" 
                        @click="acceptAffirmFn(orderIdx, order.orderId)"
                        v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"></div>
                    <router-link class="btn btn-primary" 
                        :to="{path: '/comment', query: {'orderId': order.orderId}}"
                        v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"></router-link>
                    <!-- <router-link class="btn btn-primary"
                        to="/shopCar"
                        v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"></router-link> -->
                </div>
            </div>
            <!--  -->
            <div class="html-base" ref="base"></div>
        </div>

        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popCancel">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p></p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="orderCancelFn()"></div>
                        <div class="btn btn-no" @click="popCancelFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popRemind">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p></p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="popRemindFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popPostpone">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p>{{postPoneDay}}</p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="postPoneAffirm()"></div>
                        <div class="btn btn-no" @click="popPostponeFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- - -->
        <!-- <transition name="fade">
            <div class="pop-shade" v-show="popDel"> -->
                <!--  -->
                <!-- <div class="pop-main">
                    <div class="pop-body">
                        <p></p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="delAffirm()"></div>
                        <div class="btn btn-no" @click="popDelFn()"></div>
                    </div>
                </div> -->
        <!--     </div>
        </transition> -->
        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popAffirm">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p>{{popAffirmText}}</p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="popAffirmFn()"></div>
                        <div class="btn btn-no" @click="acceptAffirmFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    name: "order-submit",
    data () {
        return {
            // 
            orderEmpty: false,
            // 
            orderTypeArr: [
                {text: '', active: true, status: 'all'},
                {text: '', active: false, status: 'paid'},
                {text: '', active: false, status: 'receipt'},
            ],
            // 
            pageNo: 1,
            // 
            orderLoaderEnd: false, 
            // 
            orderArr: [],
            // 
            orderEmpty: false,
            // -
            popAccept: false,
            // 
            orderAcceptIdx: 0,
            // -
            popCancel: false,
            // ID
            orderCancelId: null,
            // -
            popRemind: false,
            // ID
            orderRemindId: null,
            // 
            postPoneDay: null,
            // -
            popPostpone: false,
            // ID
            orderPostponeId: null,
            // -
            popDel: false,
            // ID
            orderDelId: null,
            // -
            popAffirm: false,
            // -
            popAffirmText: '',
            // id
            orderAffirmId: null
        }
    },
    created() {
        // 
        this.orderViewFn(this.pageNo, this.$route.query.status);
        // 
        this.orderStatusTextFn();
        // 
        let timeStamp = new Date().getTime();
        this.$http({
            method: 'post',
            url: this._ajaxUrl + '/order/orderSaveController/deferReceivingConfirmDay',
            headers: {
                'time-stamp': timeStamp
            },
        }).then((res) => {
            if (res.data.errorCode === '0000' && res.data.data) {
                this.postPoneDay = res.data.data
            }
        })
    },
    mounted() {
        window.addEventListener('scroll', this.scrollFn);
    },
    //
    destroyed() {
        window.removeEventListener('scroll',this.scrollFn)
    },
    methods: {
        // 
        orderViewFn: function (pageNo = 1, status = 'all') {
            let timeStamp = new Date().getTime();
            let params = {'pageNo': pageNo, 'status': status};
            console.log(params);
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            // 
            this.orderTypeArr.forEach((item) => {
                if (item.status === status) {
                    item.active = true;
                } else {
                    item.active = false;
                }
            });
            // 
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    'time-stamp': timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/order/orderSaveController/selectOrderInfoAll',
                    data: aes
                }).then((res) => {
                    // 
                    console.log(res.data.data.list)
                    if (res.data.errorCode === '0000' && res.data.data.list.length > 0) {
                        this.orderEmpty = false;
                        // 1
                        if (pageNo === 1) {
                            // 
                            this.orderArr.splice(0, this.orderArr.length);
                            this.orderArr.push(...res.data.data.list);
                            // if (this.orderArr.length <= 0) {
                            //     this.orderEmpty = true;
                            // } else {
                            //     this.orderEmpty = false;
                            // }
                        } else {
                            this.orderArr.push(...res.data.data.list);
                        }
                        // +1
                        this.pageNo ++;
                        this.orderLoaderEnd = true;
                        console.log(this.orderArr[0])
                    } else {
                        this.orderEmpty = true;
                    }
                })
            })
        },
        // 
        orderTypeFn: function (idx) {
            this.pageNo = 1;
            this.orderViewFn(this.pageNo, this.orderTypeArr[idx].status);
        },
        // 
        scrollFn: function () {
            // 
            if (this.orderLoaderEnd) {
                // 
                let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                // 
                if ((document.documentElement.clientHeight + scrollTop + 100) >= this.$refs.base.offsetTop) {
                    // 
                    let status = 'all';
                    this.orderTypeArr.forEach((item) => {
                        if (item.active) {
                            status = item.status;
                        }
                    });
                    this.orderLoaderEnd = false;
                    this.orderViewFn(this.pageNo, status);
                }
            }
        },
        // ()
        orderStatusTextFn: function (orderStatus, evaluateFlag) {
            switch(orderStatus) {
                case 0:
                case 1:
                    return '';
                    break;
                case 2:
                    return '';
                    break;
                case 3: 
                    return '';
                    break;
                case 4: 
                    return '';
                    break;
                case 10: 
                    return '';
                    break;
                case 5: 
                    return '';
                    break;
                case 6: 
                    if (evaluateFlag === -1 || evaluateFlag === 0) {
                        return ''
                    } else {
                        return ''
                    }
                    break;
                case 7: 
                    return '';
                    break;
                case 8:
                case 9:
                    return '';
                    break;
                case 11: 
                    return '';
                    break;
                default: 
                    return '' 
            }
        },
        // 
        productPriceInteger: function (parentIdx, selfIdx) {
            let productPrice = '';
            // 12
            if (this.orderArr[parentIdx].listOrderGoods[selfIdx].shoppingType === 1) {
                productPrice += this.orderArr[parentIdx].listOrderGoods[selfIdx].goodsOpenPrice;
                return productPrice.split('.')[0] || '00';
            } else {
                productPrice += this.orderArr[parentIdx].listOrderGoods[selfIdx].goodsCompletePrice;
                return productPrice.split('.')[0] || '00';
            }
        },
        // 
        productPriceDecimals: function (parentIdx, selfIdx) {
            let productPrice = '';
            // 12
            if (this.orderArr[parentIdx].listOrderGoods[selfIdx].shoppingType === 1) {
                productPrice += this.orderArr[parentIdx].listOrderGoods[selfIdx].goodsOpenPrice;
                return productPrice.split('.')[1] || '00';
            } else {
                productPrice += this.orderArr[parentIdx].listOrderGoods[selfIdx].goodsCompletePrice;
                return productPrice.split('.')[1] || '00';
            }
        },
        // 
        orderPriceInteger: function (parentIdx) {
            return this.orderArr[parentIdx].orderPrice.toString().split('.')[0] || '00';
        },
        // 
        orderPriceDecimals: function (parentIdx) {
            return this.orderArr[parentIdx].orderPrice.toString().split('.')[1] || '00';
        },
        // 
        logisticsFn: function (orderId) {
            let timeStamp = new Date().getTime();
            let params = {'orderId': orderId};
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/order/orderSaveController/selectOrderExpressDetail',
                    data: aes
                }).then((res) => {
                    if (res.data.errorCode === '0000' && res.data.data.length > 0) {
                        let orderExprEss = res.data.data[0].orderExprEss;
                        this.$router.push({
                            path: '/logistics',
                            query: {
                                orderExpressId: orderExprEss.orderExpressId,
                                orderId: orderExprEss.orderId
                            }
                        })   
                    }
                })
            })
        },
        // 
        popAcceptFn: function (orderIdx) {
            if (orderIdx || orderIdx === 0) {
                this.orderAcceptIdx = orderIdx;
            }
            this.popAccept = !this.popAccept;
        },
        // 
        acceptAffirmFn: function (orderIdx, orderId) {
            if (arguments.length > 0) {
                // id
                this.orderAffirmId = orderId;
                // 
                if (this.orderArr[orderIdx].orderPartialExpressNumber > 1) {
                    this.popAffirmText = ''
                } else {
                    this.popAffirmText = ''
                }
            }
            this.popAffirm = !this.popAffirm;
        },
        popAffirmFn: function () {
            let timeStamp = new Date().getTime();
            let params = {'orderId': this.orderAffirmId};
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/order/orderSaveController/confirmReceiptConfirm',
                    data: aes
                }).then((res) => {
                    if (res.data.errorCode === '0000' && res.data.data > 0) {
                        // 
                        let orderTypeActive = this.orderTypeArr.filter((item) => {
                            return item.active;
                        });
                        this.orderViewFn(this.pageNo, orderTypeActive[0].status);
                        this.popAffirm = !this.popAffirm;       
                    }
                })
            })
        },
        // 
        popCancelFn: function (orderIdx) {
            // orderIdxidnull
            if (arguments.length > 0) {
                this.orderCancelId = this.orderArr[orderIdx].orderId;
            } else {
                this.orderCancelId = null;
            }
            this.popCancel = !this.popCancel;
        },
        // 
        orderCancelFn: function () {
            this.$router.push({
                path: '/orderCancel',
                query: {
                    'orderId': this.orderCancelId
                }
            });
        },
        // 
        popRemindFn: function (orderId) {
            // orderIdx
            if (arguments.length > 0) {
                let timeStamp = new Date().getTime();
                let params = {'orderId': orderId};
                let md5 = this._cryptojs.MD5(JSON.stringify(params));
                this.$http({
                    method: 'post',
                    url: this._ajaxUrl + '/encryption/aes',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    data: {
                        "timeStamp": timeStamp,
                        "requestBody": JSON.stringify(params)
                    }
                }).then((res) => {
                    let aes = res.data.data.aesRequestBody;
                    this.$http({
                        method: 'post',
                        headers: {
                            'time-stamp': timeStamp,
                            'data-signature': md5
                        },
                        url: this._ajaxUrl + '/order/orderSaveController/warnDeliverConfirm',
                        data: aes
                    }).then((res) => {
                        console.log(res.data.data)
                        if (res.data.errorCode === '0000' && res.data.data > 0) {
                            this.popRemind = !this.popRemind;       
                        }
                    })
                })
            } else {
                this.popRemind = !this.popRemind;
            }
        },
        // 
        popPostponeFn: function (orderIdx, orderId) {
            this.orderPostponeId = orderId;
            this.popPostpone = !this.popPostpone;
        },
        // 
        postPoneAffirm: function () {
            let timeStamp = new Date().getTime();
            let params = {'orderId': this.orderPostponeId};
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/order/orderSaveController/deferReceivingConfirm',
                    data: aes
                }).then((res) => {
                    if (res.data.errorCode === '0000' && res.data.data > 0) {
                        this.popPostpone = !this.popPostpone;       
                    }
                })
            })
        }
    }
}
</script>

<style lang="less" scoped>
.wrap {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  background: #f6f6f6;
  // 
  .order-type-select {
    display: flex;
    flow: row nowrap;
    justify-content: center;
    width: 100%;
    height: 90px;
    padding: 0 30px;
    margin-bottom: 10px;
    background: #fff;
    li {
      display: block;
      flex: 1;
      height: 100%;
      font-size: 30px;
      text-align: center;
      line-height: 86px;
      color: #999;
      border-bottom: 3px solid #fff;
    }
    .active {
      color: #0066cc;
      border-bottom-color: #0066cc;
    }
  }
  // 
  .order-empty {
    width: 100%;
    padding-top: 640px;
    text-align: center;
    background: url('../../assets/order/order-no-bg.png') center 150px no-repeat;
    background-size: 438px 458px;
    // 
    .btn-index {
      display: inline-block;
      padding: 10px 16px;
      font-size: 28px;
      color: #fff;
      background: #0066cc;
      border-radius: 8px;
    }
  }
  // 
  .order {
    width: 100%;
    margin-bottom: 16px;
    overflow: hidden;
    // 
    .order-header {
      display: flex;
      flow: row nowrap;
      justify-content: space-between;
      width: 100%;
      height: 90px;
      padding: 0 30px;
      background: #fff;
      .header-ctn {
        height: 100%;
        font-size: 28px;
        line-height: 90px;
        white-space: nowrap;
        color: #222;
      }
    }
    // 
    .order-firm {
      height: 74px;
      width: 100%;
      padding: 0 30px;
      background: #fff;
      .firm-name {
        display: block;
        width: 100%;
        height: 100%;
        font-size: 28px;
        line-height: 74px;
        color: #666;
        background: url('../../assets/icon/icon-arrow-right.png') right center no-repeat;
        background-size: 12px 20px;
      }
    }
    // 
    .order-product {
      display: block;
      width: 100%;
      padding: 0 30px;
      background: #fffdf8;
    }
    // 
    .product-detail {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 100%;
      padding: 30px 0;
      /*border-bottom: 1px solid #e5e5e5;*/
      box-shadow: 0 -1px #e5e5e5 inset;
    }
    // 
    .product-img {
      position: relative;
      width: 178px;
      height: 178px;
      /*border: 1px solid #cccccc;*/
      box-shadow: 0 0 0 1px #ccc;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: 100%;
        max-height: 100%;
        transform: translate(-50%, -50%);
      }
      // 
      .warm-tip {
        position: absolute;
        bottom: 0;
        left: 50%;
        height: 50px;
        font-size: 22px;
        line-height: 50px;
        color: #f82222;
        white-space: nowrap;
        transform: translate(-50%, 100%);
      }
    }
    // 
    .product-infor {
      flex: 1;
      padding-left: 30px;
      .infor-div {
        width: 100%;
        margin-bottom: 6px;
        overflow: hidden;
      }
      // 
      .product-name {
        float: left;
        width: 280px;
        height: 36px;
        overflow: hidden;
        font-size: 32px;
        white-space: nowrap;
        line-height: 36px;
        text-overflow: ellipsis;
        color: #222222;
      }
      // 
      .product-price {
        float: right;
        height: 36px;
        font-size: 24px;
        white-space: nowrap;
        line-height: 36px;
        color: #f82222;
        span {
          font-size: 34px;
        }
      }
      // 
      .infor-other {
        display: block;
        width: 100%;
        li {
          display: block;
          width: 100%;
          // height: 36px;
          font-size: 24px;
          line-height: 36px;
          text-align: left;
          color: #666;
        }
      }
    }
    // 
    .product-amount {
      float: right;
      font-size: 24px;
      white-space: nowrap;
      color: #999;
      .amount-label {
        padding-right: 20px;
      }
    }
    // 
    .more {
      width: 100%;
      padding: 20px 0;
      background: #fff;
      /*border-bottom: 1px solid #ddd;*/
      box-shadow: 0 -1px #ddd inset;
      .btn-more {
        display: block;
        width: 150px;
        height: 54px;
        margin: 0 auto;
        font-size: 24px;
        text-align: center;
        line-height: 56px;
        color: #999;
        /*border: 1px solid #ddd;*/
        box-shadow: 0 0 0 1px #ddd;
        border-radius: 54px;
      }
    }
    // 
    .order-total {
      width: 100%;
      padding: 20px 30px;
      font-size: 24px;
      text-align: right;
      color: #666;
      background: #fff;
      .red {
        color: #f82222;
        span {
          font-size: 32px;
        }
      }
    }
    // 
    .order-opt {
      width: 100%;
      height: 100px;
      padding: 0 30px;
      text-align: right;
      line-height: 100px;
      background: #fff;
      /*border-top: 1px solid #e5e5e5;*/
      box-shadow: 0 1px #e5e5e5 inset;
      .btn {
        display: inline-block;
        height: 54px;
        width: 150px;
        margin-left: 15px;
        font-size: 28px;
        text-align: center;
        line-height: 58px;/*px*/
        border-radius: 54px;
      }
      // 
      .btn-default {
        color: #666;
        /*border: 1px solid #cccccc;*/
        box-shadow: 0 0 0 1px #ccc;
      }
      // 
      .btn-primary {
        color: #0066cc;
        /*border: 1px solid #0066cc;*/
        box-shadow: 0 0 0 1px #0066cc;
      }
    }
  }

  // 
  .html-base {
    width: 100%;
    height: 50px;
    background: url('../../assets/icon/icon-base.png') center center no-repeat;
    background-size: 146px 17px;
    margin-bottom: 10px;
  }

  // 
  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s ease-in;
  }
  .fade-enter, .fade-leave-to {
    opacity: 0;
  }
  // 
  .pop-shade {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .3);
    transition: all .2s ease-in;
    // 
    .pop-main {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 540px;
      overflow: hidden;
      background: #fff;
      border-radius: 10px;
      transform: translate(-50%, -80%);
    }
    .pop-body {
      width: 100%;
      padding-top: 100px;
      padding-bottom: 70px;
      font-size: 32px;
      text-align: center;
      color: #666666;
    }
    .pop-footer {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 90px;
      overflow: hidden;
      font-size: 32px;
      text-align: center;
      /*border-top: 1px solid #ccc;*/
      box-shadow: 0 -1px #ccc;
      .btn {
        display: block;
        flex: 1;
        line-height: 90px;
        /*border-right: 1px solid #ccc;*/
        box-shadow: 1px 0 #ccc;
        &.last-child {
          box-shadow: none;
        }
      }
      .btn-yes {
        color: #0066cc;
      }
      .btn-no {
         color: #666;
      }
    }
  }
}
</style>


