<template>
    <div class="wrap" v-if="dataShow">
        <div class="wrap-ctn">

            <!--  -->
            <div class="order-status">
                <div class="status-ctn">
                    <!--  -->
                    <div class="status-text">{{orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag)}}</div>
                    <!--  -->
                    <div class="status-describe"
                        v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''">223</div>
                    <div class="status-describe"
                        v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''">{{orderInfor.orderCancelRemark}}</div>
                    <div class="status-describe"
                        v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></div>
                    <div class="status-describe"
                        v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></div>
                    <div class="status-describe"
                        v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></div>
                    <div class="status-describe"
                        v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></div>
                </div>
            </div>

            <!--  -->
            <div class="receiving">
                <div class="title"></div>
                <div class="infor-detail">
                    <p class="detail-people">{{orderInfor.shippingPerson}}<span class="detail-mobile">{{orderInfor.shippingMobile}}</span></p>
                    <p class="detail-address">{{orderInfor.shippingAddress}}</p>
                    <p class="detail-message" v-if="orderInfor.customerRemark.length > 0">{{orderInfor.customerRemark}}</p>
                </div>
            </div>

            <!--  -->
            <div class="order">
                <div class="title"></div>
                <!--  -->
                <div class="order-infor">
                    <div class="infor-ctn">
                        <!-- and -->
                        <span class="order-firm">{{orderInfor.supplierThirdStoreInfoAll.companyName}}</span>
                        <span class="order-people">{{orderInfor.supplierThirdStoreInfoAll.companyContactName}}</span>
                    </div>
                </div>
                <!--  -->
                <div class="order-product">
                    <div class="product-detail" v-for="(product, productIdx) in orderInfor.listOrderGoods">
                        <!--  -->
                        <div class="product-img">
                            <img :src="product.goodsImageUrl">
                        </div>
                        <!--  -->
                        <div class="product-infor">
                            <div class="infor-div">
                                <!--  -->
                                <div class="product-name">{{product.goodsName}}</div>
                                <!--  -->
                                <div class="product-price"><span>{{productPriceInteger(productIdx)}}</span>.{{productPriceDecimals(productIdx)}}
                                </div>
                            </div>

                            <!--  -->
                            <ul class="infor-other">
                                <!--  -->
                                <li>{{product.goodsSpec}}</li>
                                <!--  -->
                                <li>{{product.goodsProducer}}</li>
                                <!--  -->
                                <li>{{product.goodsProductLotCode}}</li>
                            </ul>

                            <!--  -->
                            <div class="product-amount">
                                <span class="amount-label">{{product.shoppingType === 1 ? '' : ''}}</span>x{{product.shoppingType === 1 ? product.goodsOpenNum : product.goodsCompleteNum}}
                            </div>
                        </div>
                    </div>
                </div>
                <!--  -->
                <div class="more" v-if="orderInfor.listOrderGoods.length > 1">
                    <div class="btn-more"></div>
                </div>
            </div>

            <!--  -->
            <ul class="other-price">
                <li>
                    <div class="price-type"></div>
                    <div class="price-num"><span>{{orderInfor.expressPrice}}</span>.00</div>
                </li>
                <li>
                    <div class="price-type"></div>
                    <div class="price-num">-<span>{{orderInfor.orderPrePrice}}</span>.00</div>
                </li>
                <li>
                    <div class="price-type"></div>
                    <div class="price-num"><span>{{orderPriceInteger()}}</span>.{{orderPriceDecimals()}}</div>
                </li>
            </ul>

            <!--  -->
            <div class="order-file">
                <div class="btn-copy" @click="copyFn" data-clipboard-target=".order-num"></div>
                <p><span class="order-num">{{orderInfor.orderCode}}</span></p>
                <p>{{createTimeFn()}}</p>
            </div>

            <!--  -->
            <div class="html-base"></div>
        </div>

        <!--  -->
        <div class="order-opt">
            <!-- <div class="btn btn-default"
            v-if="orderStatusTextFn(order.orderStatus, order.evaluateFlag) === '' || orderStatusTextFn(order.orderStatus, order.evaluateFlag) === ''"></div> -->
            <div class="btn btn-default"
                v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"
                @click="popCancelFn(orderIdx)"></div>
            <router-link class="btn btn-primary" 
                to="/orderPay"
                v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></router-link>
            <div class="btn btn-default"
                v-if="(orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '') && orderInfor.orderPartialExpressNumber <= 1"
                @click="logisticsFn(orderInfor.orderId)"></div>
            <router-link class="btn btn-default" 
                v-if="(orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '') && orderInfor.orderPartialExpressNumber > 1"
                :to="{path: '/orderBatches', query: {orderCode: orderInfor.orderCode, orderId: orderInfor.orderId}}"></router-link>
            <div class="btn btn-primary"
                v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === '' || orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"
                @click="popRemindFn(orderInfor.orderId)"></div>
            <div class="btn btn-default"
                v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"
                @click="popPostponeFn(orderInfor.orderId)"></div>
            <div class="btn btn-primary" 
                @click="acceptAffirmFn(orderInfor.orderId)"
                v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></div>
            <router-link class="btn btn-primary" 
                :to="{path: '/comment', query: {'orderId': orderInfor.orderId}}"
                v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></router-link>
           <!--  <router-link class="btn btn-primary"
                to="/shopCar"
                v-if="orderStatusTextFn(orderInfor.orderStatus, orderInfor.evaluateFlag) === ''"></router-link> -->
        </div>

        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popCancel">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p></p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="orderCancelFn()"></div>
                        <div class="btn btn-no" @click="popCancelFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popRemind">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p></p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="popRemindFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popPostpone">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p>{{postPoneDay}}</p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="postPoneAffirm()"></div>
                        <div class="btn btn-no" @click="popPostponeFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popDel">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p></p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="delAffirm()"></div>
                        <div class="btn btn-no" @click="popDelFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- - -->
        <transition name="fade">
            <div class="pop-shade" v-show="popAffirm">
                <!--  -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p>{{popAffirmText}}</p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="popAffirmFn()"></div>
                        <div class="btn btn-no" @click="acceptAffirmFn()"></div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import Clipboard from 'clipboard';
import {Toast} from 'mint-ui';
export default {
    name: "order-submit",
    data() {
        return {
            // 
            orderInfor: {},
            // 
            orderEmpty: false,
            // -
            popAccept: false,
            // 
            orderAcceptIdx: 0,
            // -
            popCancel: false,
            // ID
            orderCancelId: null,
            // -
            popRemind: false,
            // ID
            orderRemindId: null,
            // 
            postPoneDay: null,
            // -
            popPostpone: false,
            // ID
            orderPostponeId: null,
            // -
            popDel: false,
            // ID
            orderDelId: null,
            // -
            popAffirm: false,
            // -
            popAffirmText: '',
            // id
            orderAffirmId: null,
            // 
            dataShow: true
        }
    },
    created () {
        // 
        let timeStamp = new Date().getTime();
        let params = {'orderId': this.$route.query.orderId};
        let md5 = this._cryptojs.MD5(JSON.stringify(params));
        this.$http({
            method: 'post',
            url: this._ajaxUrl + '/encryption/aes',
            headers: {
                'time-stamp': timeStamp,
                'data-signature': md5
            },
            data: {
                'timeStamp': timeStamp,
                "requestBody": JSON.stringify(params)
            }
        }).then((res) => {
            let aes = res.data.data.aesRequestBody;
            this.$http({
                method: 'post',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                url: this._ajaxUrl + '/order/orderSaveController/selectOrderDetail',
                data: aes
            }).then((res) => {
                if (res.data.errorCode === '0000') {
                    this.orderInfor = res.data.data.resultBean;
                    console.log(this.orderInfor)
                }
            })
        })
        // 
        this.$http({
            method: 'post',
            url: this._ajaxUrl + '/order/orderSaveController/deferReceivingConfirmDay',
            headers: {
                'time-stamp': timeStamp
            },
        }).then((res) => {
            if (res.data.errorCode === '0000' && res.data.data) {
                this.postPoneDay = res.data.data
            }
        });
    },
    methods: {
        //
        copyFn(){
            const clipboard = new Clipboard('.btn-copy');
            clipboard.on('success',function (e) {
                Toast({
                    message: '',
                    position: 'bottom',
                    duration: 3000,
                    className: 'copy-success'
                });
            })
        },
        // ()
        orderStatusTextFn: function (orderStatus, evaluateFlag) {
            switch(orderStatus) {
            case 0:
            case 1:
                return '';
                break;
            case 2:
                return '';
                break;
            case 3: 
                return '';
                break;
            case 4: 
                return '';
                break;
            case 10: 
                return '';
                break;
            case 5: 
                return '';
                break;
            case 6: 
                if (evaluateFlag === -1 || evaluateFlag === 0) {
                    return ''
                } else {
                    return ''
                }
                break;
            case 7: 
                return '';
                break;
            case 8:
            case 9:
                return '';
                break;
            case 11: 
                return '';
                break;
            default: 
                return '' 
            }
        },
        // 
        productPriceInteger: function (selfIdx) {
            let productPrice = '';
            // 12
            if (this.orderInfor.listOrderGoods[selfIdx].shoppingType === 1) {
                productPrice += this.orderInfor.listOrderGoods[selfIdx].goodsOpenPrice;
                return productPrice.split('.')[0] || '00';
            } else {
                productPrice += this.orderInfor.listOrderGoods[selfIdx].goodsCompletePrice;
                return productPrice.split('.')[0] || '00';
            }
        },
        // 
        productPriceDecimals: function (selfIdx) {
            let productPrice = '';
            // 12
            if (this.orderInfor.listOrderGoods[selfIdx].shoppingType === 1) {
                productPrice += this.orderInfor.listOrderGoods[selfIdx].goodsOpenPrice;
                return productPrice.split('.')[1] || '00';
            } else {
                productPrice += this.orderInfor.listOrderGoods[selfIdx].goodsCompletePrice;
                return productPrice.split('.')[1] || '00';
            }
        },
        // 
        orderPriceInteger: function () {
            return this.orderInfor.orderPrice.toString().split('.')[0] || '00';
        },
        // 
        orderPriceDecimals: function () {
            return this.orderInfor.orderPrice.toString().split('.')[1] || '00';
        },
        // 
        createTimeFn: function () {
            var date = new Date(this.orderInfor.createTime);
            return `${date.getFullYear()}-${date.getMonth() + 1 > 10 ? (date.getMonth() + 1) : '0' + (date.getMonth() + 1)}-${date.getDate() > 10 ? date.getDate() : '0' + date.getDate()} ${date.getHours() > 10 ? date.getHours() : '0' + date.getHours()}:${date.getMinutes() > 10 ? date.getMinutes() : '0' + date.getMinutes()}`;
        },
        // 
        logisticsFn: function (orderId) {
            let timeStamp = new Date().getTime();
            let params = {'orderId': orderId};
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/order/orderSaveController/selectOrderExpressDetail',
                    data: aes
                }).then((res) => {
                    if (res.data.errorCode === '0000' && res.data.data.length > 0) {
                        let orderExprEss = res.data.data[0].orderExprEss;
                        this.$router.push({
                            path: '/logistics',
                            query: {
                                orderExpressId: orderExprEss.orderExpressId,
                                orderId: orderExprEss.orderId
                            }
                        })   
                    }
                })
            })
        },
        // 
        popAcceptFn: function (orderIdx) {
            if (orderIdx || orderIdx === 0) {
                this.orderAcceptIdx = orderIdx;
            }
            this.popAccept = !this.popAccept;
        },
        // 
        acceptAffirmFn: function (orderId) {
            if (arguments.length > 0) {
                // id
                this.orderAffirmId = orderId;
                // 
                if (this.orderInfor.orderPartialExpressNumber > 1) {
                    this.popAffirmText = ''
                } else {
                    this.popAffirmText = ''
                }
            }
            this.popAffirm = !this.popAffirm;
        },
        popAffirmFn: function () {
            let timeStamp = new Date().getTime();
            let params = {'orderId': this.orderAffirmId};
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/order/orderSaveController/confirmReceiptConfirm',
                    data: aes
                }).then((res) => {
                    if (res.data.errorCode === '0000' && res.data.data > 0) {
                        this.popAffirm = !this.popAffirm;       
                    }
                })
            })
        },
        // 
        popCancelFn: function (orderIdx) {
            // orderIdxidnull
            if (arguments.length > 0) {
                this.orderCancelId = this.orderInfor.orderId;
            } else {
                this.orderCancelId = null;
            }
            this.popCancel = !this.popCancel;
        },
        // 
        orderCancelFn: function () {
            // this.orderCancelId
            this.$router.push({
                path: '/orderCancel',
                query: {
                    'orderId': this.orderCancelId
                }
            });
        },
        // 
        popRemindFn: function (orderId) {
            // orderIdx
            if (arguments.length > 0) {
                let timeStamp = new Date().getTime();
                let params = {'orderId': orderId};
                let md5 = this._cryptojs.MD5(JSON.stringify(params));
                this.$http({
                    method: 'post',
                    url: this._ajaxUrl + '/encryption/aes',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    data: {
                        "timeStamp": timeStamp,
                        "requestBody": JSON.stringify(params)
                    }
                }).then((res) => {
                    let aes = res.data.data.aesRequestBody;
                    this.$http({
                        method: 'post',
                        headers: {
                            'time-stamp': timeStamp,
                            'data-signature': md5
                        },
                        url: this._ajaxUrl + '/order/orderSaveController/warnDeliverConfirm',
                        data: aes
                    }).then((res) => {
                        console.log(res.data.data)
                        if (res.data.errorCode === '0000' && res.data.data > 0) {
                            this.popRemind = !this.popRemind;       
                        }
                    })
                })
            } else {
                this.popRemind = !this.popRemind;
            }
        },
        // 
        popPostponeFn: function (orderId) {
            this.orderPostponeId = orderId;
            this.popPostpone = !this.popPostpone;
        },
        // 
        postPoneAffirm: function () {
            let timeStamp = new Date().getTime();
            let params = {'orderId': this.orderPostponeId};
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/order/orderSaveController/deferReceivingConfirm',
                    data: aes
                }).then((res) => {
                    if (res.data.errorCode === '0000' && res.data.data > 0) {
                        this.popPostpone = !this.popPostpone;       
                    }
                })
            })
        }
    }
}
</script>

<style lang="less" scoped>
    .wrap {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
        padding-bottom: 101px;
        background: #f6f6f6;
        .wrap-ctn {
            width: 100%;
            height: 100%;
            overflow-x: hidden;
            overflow-y: auto;
        }
        // 
        .order-status {
            position: relative;
            width: 100%;
            height: 240px;
            text-align: left;
            background: url('../../assets/order/order-status.png') center center no-repeat;
            background-size: cover;
            .status-ctn {
                position: absolute;
                top: 50%;
                left: 88px;
                display: inline-block;
                line-height: 1;
                color: #fff;
                transform: translateY(-50%);
            }
            // 
            .status-text {
                font-size: 32px;
                margin-bottom: 26px;
            }
            // 
            .status-describe {
                font-size: 24px;
            }
        }

        .title {
            width: 100%;
            height: 60px; /*px*/
            padding: 0 30px;
            font-size: 28px; /*px*/
            line-height: 60px; /*px*/
            color: #222;
            background: #fff;
            /*border-bottom: 1px solid #ddd;*/
            box-shadow: 0 -1px #ddd inset;
        }

        // 
        .receiving {
            text-align: left;
            background: #fff;
            // 
            .infor-detail {
                padding: 30px;
                font-size: 28px;
                line-height: 50px;
                color: #666;
                p {
                    padding-left: 34px;
                    overflow: hidden;
                }
            }
            // 
            .detail-people {
                background: url('../../assets/icon/icon-link.png') left (50-25)/2px no-repeat;
                background-size: 23px 25px;
                // 
                .detail-mobile {
                    float: right;
                }
            }
            // 
            .detail-address {
                background: url('../../assets/icon/icon-pos.png') left (50-30)/2px no-repeat;
                background-size: 22px 30px;
            }
            // 
            .detail-message {
                background: url('../../assets/icon/icon-message.png') left (50-22)/2px no-repeat;
                background-size: 24px 22px;
            }
        }

        // 
        .order {
            width: 100%;
            margin-top: 16px;
            overflow: hidden;
            /*border-top: 1px solid #e5e5e5;*/
            /*border-bottom: 1px solid #e5e5e5;*/
            box-shadow: 0 -1px #e5e5e5 inset, 0 1px #e5e5e5 inset;

            // 
            .order-infor {
                height: 74px;
                width: 100%;
                padding: 0 30px;
                background: #fff;
                .infor-ctn {
                    width: 100%;
                    height: 100%;
                    font-size: 28px;
                    line-height: 74px;
                    color: #666;
                    background: url('../../assets/icon/icon-arrow-right.png') right center no-repeat;
                    background-size: 12px 20px;
                    .order-firm {
                        padding-right: 30px;
                    }
                }
            }
            // 
            .order-product {
                display: block;
                width: 100%;
                padding: 0 30px;
                background: #fffdf8;
            }
            // 
            .product-detail {
                display: flex;
                flex-direction: row;
                width: 100%;
                height: 100%;
                padding: 30px 0;
                /*border-bottom: 1px solid #e5e5e5;*/
                box-shadow: 0 -1px  #e5e5e5 inset;

            }
            // 
            .product-img {
                position: relative;
                width: 178px;
                height: 178px;
                /*border: 1px solid #cccccc;*/
                box-shadow: 0 0 0 1px #ccc;
                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    max-width: 100%;
                    max-height: 100%;
                    transform: translate(-50%, -50%);
                }
                // 
                .warm-tip {
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    height: 50px;
                    font-size: 22px;
                    line-height: 50px;
                    color: #f82222;
                    white-space: nowrap;
                    transform: translate(-50%, 100%);
                }
            }
            // 
            .product-infor {
                flex: 1;
                padding-left: 30px;
                .infor-div {
                    width: 100%;
                    margin-bottom: 6px;
                    overflow: hidden;
                }
                // 
                .product-name {
                    float: left;
                    width: 280px;
                    height: 36px;
                    overflow: hidden;
                    font-size: 32px;
                    white-space: nowrap;
                    line-height: 36px;
                    text-overflow: ellipsis;
                    color: #222222;
                }
                // 
                .product-price {
                    float: right;
                    height: 36px;
                    font-size: 24px;
                    white-space: nowrap;
                    line-height: 36px;
                    color: #f82222;
                    span {
                        font-size: 34px;
                    }
                }
                // 
                .infor-other {
                    display: block;
                    width: 100%;
                    li {
                        display: block;
                        width: 100%;
                        // height: 36px;
                        font-size: 24px;
                        line-height: 36px;
                        text-align: left;
                        color: #666;
                    }
                }
            }
            // 
            .product-amount {
                float: right;
                font-size: 24px;
                white-space: nowrap;
                color: #999;
                .amount-label {
                    padding-right: 20px;
                }
            }
            // 
            .more {
                width: 100%;
                padding: 20px 0;
                background: #fff;


                .btn-more {
                    width: 150px;
                    height: 54px;
                    margin: 0 auto;
                    font-size: 24px;
                    text-align: center;
                    line-height: 56px;
                    color: #999;
                    /*border: 1px solid #ddd;*/
                    box-shadow: 0 0 0 1px #ddd;
                    border-radius: 54px;
                }
            }
        }
        // 
        .other-price {
            display: block;
            width: 100%;
            padding: 20px 30px;
            margin-top: 16px;
            margin-bottom: 16px;
            background: #fff;
            /*border-top: 1px solid #e5e5e5;*/
            /*border-bottom: 1px solid #e5e5e5;*/
            box-shadow: 0 -1px #e5e5e5 inset,0 1px #e5e5e5 inset;
            li {
                display: flex;
                flex-flow: row nowrap;
                justify-content: space-between;
                font-size: 28px;
                line-height: 50px;
                color: #666;
                .price-num {
                    font-size: 24px;
                    span {
                        font-size: 28px;
                    }
                }
                &:last-child .price-num {
                    color: #f82222;
                }
            }
        }
        // 
        .order-file {
            padding: 15px 30px;
            background: #fff;
            p {
                overflow: hidden;
                font-size: 24px;
                text-align: left;
                line-height: 46px;
                color: #999;
            }
            // 
            .btn-copy {
                display: block;
                float: right;
                width: 104px;
                height: 42px;
                font-size: 22px;
                text-align: center;
                line-height: 44px;/*px*/
                color: #999;
                border: none;
                /*border: 1px solid #ddd;*/
                box-shadow: 0 0 0 1px #ddd;
                border-radius: 5px;
                background: #fff;
            }
        }
        // 
        .html-base {
            width: 100%;
            height: 50px;
            background: url('../../assets/icon/icon-base.png') center center no-repeat;
            background-size: 146px 17px;
        }
        // 
        .order-opt {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100px;
            padding: 0 30px;
            text-align: right;
            line-height: 100px;
            background: #fff;
            /*border-top: 1px solid #e5e5e5;*/
            box-shadow: 0 1px #e5e5e5 inset;
            .btn {
                display: inline-block;
                height: 54px;
                width: 150px;
                margin-left: 15px;
                font-size: 28px;
                text-align: center;
                line-height: 58px;/*px*/
                border-radius: 54px;
            }
            // 
            .btn-default {
                color: #666;
                /*border: 1px solid #cccccc;*/
                box-shadow: 0 0 0 1px #ccc;
            }
            // 
            .btn-primary {
                color: #0066cc;
                /*border: 1px solid #0066cc;*/
                box-shadow: 0 0 0 1px #0066cc;
            }
        }

        // 
        .fade-enter-active, .fade-leave-active {
            transition: opacity .3s ease-in;
        }
        .fade-enter, .fade-leave-to {
            opacity: 0;
        }
        // 
        .pop-shade {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 9999;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, .3);
            transition: all .2s ease-in;
            // 
            .pop-main {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 540px;
                overflow: hidden;
                background: #fff;
                border-radius: 10px;
                transform: translate(-50%, -80%);
            }
            .pop-body {
                width: 100%;
                padding-top: 100px;
                padding-bottom: 70px;
                font-size: 32px;
                text-align: center;
                color: #666666;
            }
            .pop-footer {
                display: flex;
                flex-direction: row;
                width: 100%;
                height: 90px;
                overflow: hidden;
                font-size: 32px;
                text-align: center;
                /*border-top: 1px solid #ccc;*/
                box-shadow: 0 1px #ccc inset;
                .btn {
                    display: block;
                    flex: 1;
                    line-height: 90px;
                    /*border-right: 1px solid #ccc;*/
                    box-shadow: -1px 0 #ccc inset;
                    &.last-child {
                        box-shadow: none;
                    }
                }
                .btn-yes {
                    color: #0066cc;
                }
                .btn-no {
                    color: #666;
                }
            }
        }
    }
</style>


