<template>
	<div class="wrap" :class="{active:isShowDelect}">
		<!--  -->
		<div class="footprint-more" v-if="hasFootprint">
			<!-- top -->
			<div class="page-top">
				<div class="record"> <span class="red">{{totalGoods}}</span> 30</div>
				<div class="btn-edit" @click="goodEditBtn()">
					<span v-if="isShowDelect"></span>
					<span v-else></span>
				</div>
			</div>

			<!--  -->
			<ul class="goods-list">
				<li v-for="(item,listIndex) in goodArr" :class="{active:isShowDelect}">
					<div class="good-select" :class="{active:item.goodActive}" v-show="isShowDelect" @click="goodSelect(listIndex)">
					</div>
					<router-link :to="{path: '/goodsDetail', query: {goodsId: item.goodsInfo.goodsId}}">
						<!--  -->
						<div class="photo">
							<img :src="item.goodsInfo.goodsImg">
						</div>
						<div class="detail">
							<!--  -->
							<div class="good-name">{{item.goodsInfo.goodsName}}</div>
							<!--  -->
							<div class="good-size">{{item.goodsInfo.spec}}</div>
							<!--  -->
							<div class="good-vender">{{item.goodsInfo.producer}}</div>
							<!--  -->
							<div class="good-price" v-if="goodPrice(listIndex)">{{goodPrice(listIndex)}}</div>
							<!--  -->
							<div class="good-price" v-else="goodPrice(listIndex)"><span>{{goodPriceInteger(item.goodsInfo.goodsProductLimitPrice)}}</span>.{{goodPriceDecimals(item.goodsInfo.goodsProductLimitPrice)}}</div>
						</div>
					</router-link>
				</li>
			</ul>
			<!--  -->
			<div class="loading" v-show="isLoading"></div>
			<!--  -->
			<div class="list-end" v-show="isEnd"> >"<  ~ </div>
			<!--  --> 
			<div class="select-delete" v-show="isShowDelect">
				<div class="select-all" :class="{active:allSelect}" @click="allSelectFn()"><input type="checkbox" v-model="allSelect"></div>
				<div class="btn-delete"><span :class="{active:activeDelete}" @click="deleteFn()"></span></div>
			</div>
		</div>
		<!--  -->
		<div class="footprint-none" v-else>
			<!--  -->
    		<div class="none-photo"></div>
    		<p class="none-text">~</p>
    		<router-link to='/home' class="link"></router-link>
		</div>
	</div>
</template>
<script>
	import {Toast} from 'mint-ui';
	import Vue from 'vue';
	export default {
		name:"my-footprint",
		data(){
			return{
				//
				hasFootprint:true,
				//
				isShowDelect: false,
				//
				allSelect:false,
				//
				activeDelete:false,
				//
				goodArr:[],
				//
				pageNo:1,
				//
				pageSize:5,
				//
				isEnd:false,
				//
				isLoading:false,
				//
				totalGoods: null,
				//
				numberTip: ''
			}
		},
		methods:{
			//
			footPrintViewFn: function(){
				this.pageNo = 1;
				let timeStamp = new Date().getTime();
				let params = {'pageNo' : this.pageNo, 'pageSize' : this.pageSize};
				this._signature = this._cryptojs.MD5(JSON.stringify(params));
				this.$http({
					method: 'post',
					url: this._ajaxUrl + '/encryption/aes',
					headers: {
						'data-signature': this._signature
					},
					data: {
						"time-stamp": timeStamp,
		                "requestBody": JSON.stringify(params)
					}
				}).then((res) => {
					let aes = res.data.data.aesRequestBody;
	            	this.$http({
	            		method: 'post',
	                    headers: {
	                    	"time-stamp": timeStamp,
	                        'data-signature': this._signature
	                    },
	                    url: this._ajaxUrl + '/goods/footprint/pagingQueryMyGoodsFootprintList',
	                    data: aes
	            	}).then((res) => {
	            		//
	            		this.goodArr.splice(0,this.goodArr.length);
	            		this.goodArr.push(...res.data.data.list);
	            		// 
				        this.totalGoods = res.data.data.rows;
				        //
			            this.numberTip = res.data.data.numberTip;
	            		//
	                    for (var i = 0; i < this.goodArr.length; i++) {
							Vue.set(this.goodArr[i], 'goodActive', false);
	                    }
	            	});
				});
			},
			//
			loadMore:function(){
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				let windowHeight  = document.body.offsetHeight;
				let documentHeight  = document.body.scrollHeight;
				//
				if(documentHeight - (scrollTop + windowHeight) < windowHeight){
					//  
					if(this.isLoading || this.isEnd){
						return false;
					}
					//
					this.isLoading = true;
					this.pageNo++;
					let timeStamp = new Date().getTime();
					let params = {'pageNo' : this.pageNo, 'pageSize' : this.pageSize};
					this._signature = this._cryptojs.MD5(JSON.stringify(params));
					this.$http({
						method: 'post',
						url: this._ajaxUrl + '/encryption/aes',
						headers: {
							'data-signature': this._signature
						},
						data: {
							"time-stamp": timeStamp,
			                "requestBody": JSON.stringify(params)
						}
					}).then((res) => {
						let aes = res.data.data.aesRequestBody;
		            	this.$http({
		            		method: 'post',
		                    headers: {
		                    	"time-stamp": timeStamp,
		                        'data-signature': this._signature
		                    },
		                    url: this._ajaxUrl + '/goods/footprint/pagingQueryMyGoodsFootprintList',
		                    data: aes
		            	}).then((res) => {
		            		if (res.data.errorCode == '0000') {
			            		//
			            		let oldLength= this.goodArr.length;
			            		this.goodArr.push(...res.data.data.list);
			            		//
			                    for (var i = oldLength; i < this.goodArr.length; i++) {
									Vue.set(this.goodArr[i], 'goodActive', false);
			                    }
			                    //
								this.isLoading = false;
			                    //
								if(this.pageNo * this.pageSize >= this.totalGoods){
									this.isEnd = true;
								}
							}
		            	});
					});
				}
			},
			// 
			goodPrice: function(listIndex){
				if(this.numberTip == 3){
					//
					return ''
				}else if(this.numberTip == 2){
					//
					switch(this.goodArr[listIndex].goodsInfo.isLimitGoodsStatus)
						{
							case 5:
								return ''
								break;
							case 6:
								return ''
								break;
							case 0:
								//
								return false;
								break; 
						}
				}
			},
			//
			goodPriceInteger: function(price){
				if(price){
					return price.openOrCompleteSellMoney.toFixed(2).toString().split('.')[0];
				}
			},
			//
			goodPriceDecimals: function(price){
				if(price){
					return price.openOrCompleteSellMoney.toFixed(2).toString().split('.')[1];
				}
			},
			// 
			goodEditBtn:function(){
				this.isShowDelect = !this.isShowDelect;
			},
			// 
			goodSelect:function(listIndex){
				//
				this.goodArr[listIndex].goodActive = !this.goodArr[listIndex].goodActive;
				//
				let goodLenth = this.goodArr.length;
				//
				let selectLenth = 0;
				for (let i = 0; i < goodLenth; i++) {
					if(this.goodArr[i].goodActive){
						selectLenth++;
					}
				}
				if(selectLenth == goodLenth){
					//
					this.allSelect = true;
				}else{
					//
					this.allSelect = false;
				}
				//
				if(selectLenth > 0){
					this.activeDelete = true;
				}else{
					this.activeDelete = false;
				}
			},
			//
			allSelectFn:function(){
				this.allSelect = !this.allSelect;
				//
				if(this.allSelect){
					for (let i = 0; i < this.goodArr.length; i++) {
						this.goodArr[i].goodActive = true;
						this.activeDelete = true;
					}
				}else{
					for (let i = 0; i < this.goodArr.length; i++) {
						//
						this.goodArr[i].goodActive = false;
						this.activeDelete = false;
					}
				}
			},
			//
			deleteFn:function(){
			    if(this.activeDelete){
                    //id
					let footprintIds = '';
                    for (let i = 0; i < this.goodArr.length; i++) {
                        if(this.goodArr[i].goodActive){
                            footprintIds += this.goodArr[i].footprintId + ',';
						}
                    }
                    //
                    let timeStamp = new Date().getTime();
                    let params = {'goodsIds' : footprintIds};
	                this._signature = this._cryptojs.MD5(JSON.stringify(params));
	                this.$http({
	                    method: 'post',
	                    url: this._ajaxUrl + '/encryption/aes',
	                    headers: {
                            'data-signature': this._signature
                        },
                        data: {
                            "time-stamp": timeStamp,
                            "requestBody": JSON.stringify(params)
                        }
	                }).then((res) => {
	                	let aes = res.data.data.aesRequestBody;
	                	this.$http({
	                		method: 'post',
                            headers: {
                            	"time-stamp": timeStamp,
                                'data-signature': this._signature
                            },
                            url: this._ajaxUrl + '/goods/footprint/batchRemove',
                            data: aes
	                	}).then((res) => {
		                    if (res.data.errorCode == '0000') {
		                    	//
		                        for (let i = this.goodArr.length - 1; i >= 0; i--) {
				                    if (this.goodArr[i].goodActive) {
				                        this.goodArr.splice(i, 1);
				                    }
				                }
				                this.activeDelete = false;
				                //
		                     	this.footPrintViewFn();
		                    }
		                    //
		                    Toast({
	                            message: res.data.message,
	                            position: 'bottom',
	                            duration: 3000,
	                            className: 'public'
	                        });
	                	});
	                });
				}
			}
		},
		created(){
			// 
			this.footPrintViewFn();
		},
		mounted() {
            window.addEventListener('touchmove', this.loadMore)
        }
	}
</script>
<style lang="less" scoped>
	.wrap{
	    width: 100%;
	    background: #f6f6f6;
	    &.active{
	   		padding-bottom: 100px;
	    }
	}
	/*  */
	.page-top{
		height: 78px;/*px*/
		line-height: 78px;/*px*/
		background-color: #f6f6f6;
		padding: 0 30px;
		font-size: 28px;/*px*/
		overflow: hidden;
		.record{
			float: left;
			color:#999;
		}
		.btn-edit{
			float: right;
			color:#666;
		}
		.red{
			color:#ff0000;
		}
	}
	/*  */
	.goods-list{
		background-color: #fff;
		li{
			box-shadow: 0 -1px #e5e5e5 inset;
			display: flex;
			position: relative;
			padding: 32px 30px;
			&.active{
				padding: 32px 30px 32px 96px;
			}
			a{
				width: 100%;
				display: -webkit-box;
			}
			.good-select{
				position: absolute;
				left: 30px;
				top:0;
				width: 66px;
				height: 100%;
				background: url('../../assets/icon/icon-select.png') left center no-repeat;
				background-size: 40px 40px;
		        &.active{
		        	background: url('../../assets/icon/icon-select-active.png') left center no-repeat;
		        	background-size: 40px 40px;
		        }
				input{
					display: none;
				}
			}
			.photo{
				width: 200px;
				flex: 0 0 auto;
                -webkit-flex: 0 0 auto;
				img{
					width: 100%;
				}
			}
			.detail{	
				padding-left: 28px;
				flex: 1 1 auto;
                -webkit-flex: 1 1 auto;
				overflow: hidden;
			}
			.good-name{
				width: 100%;
				line-height: 50px;/*px*/
				font-size: 28px;/*px*/
				color:#222;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			.good-size,.good-vender{
				line-height: 40px;/*px*/
				font-size: 24px;/*px*/
				color:#999;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			.good-price{
				font-size: 28px;/*px*/
				color:#ff0101;
				margin-top: 30px;
				span{
					font-size: 34px;/*px*/
				}
			}
		}
	}
	/*  */
	.list-end{
        color: #bbb;
        text-align: center;
        line-height: 70px;/*px*/
        font-size: 18px;/*px*/
    }
    /*  */
    .select-delete{
    	width: 100%;
    	height: 100px;/*px*/
    	line-height: 100px;/*px*/
		background-color: #fff;
		box-shadow: 0 1px #e5e5e5 inset;
		padding: 0 30px;
		display: flex;
        align-items:center;
		position: fixed;
		left: 0;
		bottom: 0;
    }
    /*  */
    .select-all{
    	flex:1;
    	height: 100%;
    	font-size: 28px;/*px*/
    	text-align: left;
    	padding-left: 56px;
    	color:#666;
    	background: url('../../assets/icon/icon-select.png') 0 center no-repeat;
    	background-size: 40px 40px;
        &.active{
          background: url('../../assets/icon/icon-select-active.png') 0 center no-repeat;
          background-size: 40px 40px;
        }
        input{
        	display: none;
        }
    }
    /*  */
    .btn-delete{
    	flex:1;
    	text-align: right;
    	span{
	    	width: 210px;
	    	line-height: 62px;/*px*/
	    	text-align: center;
			font-size: 30px;/*px*/
			color:#fff;
			background-color: #ccc;
			border-radius: 40px;
			display: inline-block;
			&.active{
				color:#fff;
				background-color: #fa8c35;
			}
    	}
    }
    /*  */
	.footprint-none{
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		.none-photo{
			width: 410px;
			height: 368px;
			background: url(../../assets/mine/footprint-none.png) no-repeat 0 0;
			background-size: 410px 368px;
			margin: 165px auto 0;
		}
		.none-text{
			font-size: 28px;/*px*/
			color:#666;
			line-height: 90px;/*px*/
			text-align: center;
		}
		.link{
			width: 168px;
			line-height: 48px;/*px*/
			text-align: center;
			color:#fff;
			background-color: #0066cc;
			font-size:28px;/*px*/
			margin:0 auto;
			display: block;
			border-radius: 10px;
		}
	}
	/*  */
    .loading{
		color: #bbb;
        text-align: center;
        line-height: 70px;/*px*/
        font-size: 18px;/*px*/ 
    }
</style>


